.home{
    display: flex;
    list-style: none;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.home li{
    margin: 0 50px;
    text-align: center;
    line-height: 70px;
}

.home li a{
    display: inline-block;
    width: 200px;
    height: 70px;
    font-size: 30px;
    text-decoration: none;
    border-radius: 35px;
}

.home li .HostButton{
    background-color: #888;
    color: #fff;
}
.home li .HostButton:hover{
    background-color: #333;
}

.home li .clientButton{
    border: 1px solid #3f3f3f;
    color: #0c0c0c;
}
.home li .clientButton:hover{
    background-color: #888;
}