.half_container{
    width: 50%;
    height: 100%;
    padding: 10px;
}
.half_container:first-child{
    border-right: 1px solid #ccc;
}
.sub_title{
    margin-bottom: 20px;
}

.token_div{
   margin-top: 50%;
}

.token_div p {
    font-size: 25px;   
}

.token_div .token{
    margin: 20px 0;
    padding: 5px;
    font-size: 20px;
    min-height: 40px;
    border-bottom : 1px solid #666
}

.token_div button{
    display: block;
    margin: 70% auto 0;
    width: 200px;
    height: 50px;
    font-size: 30px;
    text-decoration: none;
    border-radius: 35px;
    border: none;
    box-shadow: 0 3px 5px 0 rgba(0,0,0,0.2)
    ,0 3px 10px 0 rgba(0,0,0,0.19);
}
.token_div button:hover{
    cursor: pointer;
}

.center{
    margin-top: calc(100% - 80px);
    text-align: center;
}
.devices{
    position: relative;
   
    
}
.devices ul{
    list-style: none;
    max-height:calc(100% - 80px);
    overflow: scroll;
}

.devices ul li{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
}

.devices ul li:last-child{
    border:none;
}

.devices ul p{
    display: inline-block;
}

.devices button{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%,0);
}