@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

html,
body,
#root{
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  text-decoration: none;
  color: black;
}
.home{
    display: flex;
    list-style: none;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.home li{
    margin: 0 50px;
    text-align: center;
    line-height: 70px;
}

.home li a{
    display: inline-block;
    width: 200px;
    height: 70px;
    font-size: 30px;
    text-decoration: none;
    border-radius: 35px;
}

.home li .HostButton{
    background-color: #888;
    color: #fff;
}
.home li .HostButton:hover{
    background-color: #333;
}

.home li .clientButton{
    border: 1px solid #3f3f3f;
    color: #0c0c0c;
}
.home li .clientButton:hover{
    background-color: #888;
}
.half_container{
    width: 50%;
    height: 100%;
    padding: 10px;
}
.half_container:first-child{
    border-right: 1px solid #ccc;
}
.sub_title{
    margin-bottom: 20px;
}

.token_div{
   margin-top: 50%;
}

.token_div p {
    font-size: 25px;   
}

.token_div .token{
    margin: 20px 0;
    padding: 5px;
    font-size: 20px;
    min-height: 40px;
    border-bottom : 1px solid #666
}

.token_div button{
    display: block;
    margin: 70% auto 0;
    width: 200px;
    height: 50px;
    font-size: 30px;
    text-decoration: none;
    border-radius: 35px;
    border: none;
    box-shadow: 0 3px 5px 0 rgba(0,0,0,0.2)
    ,0 3px 10px 0 rgba(0,0,0,0.19);
}
.token_div button:hover{
    cursor: pointer;
}

.center{
    margin-top: calc(100% - 80px);
    text-align: center;
}
.devices{
    position: relative;
   
    
}
.devices ul{
    list-style: none;
    max-height:calc(100% - 80px);
    overflow: scroll;
}

.devices ul li{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
}

.devices ul li:last-child{
    border:none;
}

.devices ul p{
    display: inline-block;
}

.devices button{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%,0);
}
.client_center{
    margin : 0 auto;
    margin-top: calc(50% - 80px);

}
.client_center p{
    display: inline-block;
    font-size: 25px;
}
.client_center .client_token{
    font-size: 20px;
    border : none;
    border-bottom: 1px solid #999;
    margin: 10px;
}

.main_title{
    font-weight: 100;
    text-align: center;
}

.title{
    font-size: 40px;
    margin: 0 30px 10px 30px;
    padding: 10px 0;
}

.rapper{
    height: 100%;
    padding-top: 20px;
}

.container{
    display: flex;
    height: calc(100% - 110px);
    margin: 0 20px;
    border-top: 1px solid #ccc;
}
